body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
  
#root,
.App,
#map {
  width: 100%;
  height: 100%;
}
.welcome {
  position: fixed;
  overflow: auto;
  /* text-align: center; */
  max-height: 90%;
  left: 20px;
  top: 20px;
  color: white;
  padding: 10px;
  font-family: "Futura", cursive;
  font-size: 12px;
  padding: 10px;
  margin: 0;
  color: #ededed;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(25, 25, 25, 0.5);
  border-radius: 16px;
  font-size: 16px;
  width: 200px;
  max-width: 30%;
  z-index: 1;
}
.slider {
  width: 90%;
}
.banner {
  position: fixed;
  overflow: auto;
  text-align: center;
  bottom: 0px;
  color: white;
  left: 45%;
  padding: 5px;

  appearance: none;
  background: none;
  border: none;
  outline: none;
  border-radius: 5px 5px 0px 0px;
  font-size: 12px;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.25);
  opacity: 0.8;
}
.banner a {
  text-decoration: none;
  color: rgb(114, 144, 201);
}
.welcome p {
  font-size: 14px;
}
.path {
  position: fixed;
  overflow: auto;
  max-height: 90%;
  right: 20px;
  top: 20px;
  color: white;
  padding: 10px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(25, 25, 25, 0.5);
  border-radius: 16px;
  font-size: 16px;
  max-width: 30%;
  z-index: 1;
}
.welcome h4,
.path h4 {
  text-align: left;
  font-size: 20px;
  border-bottom: 3px solid rgba(255, 255, 255, 0.25);
}
.welcome p:not(:first-child) {
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}
.slider-container{
  border-top: 1px solid rgba(255, 255, 255, 0.25);
}

.path h4 {
  text-align: right;
}
.right-table-cell {
  text-align: right;
}
.search-box .search-bar {
  padding: 10px;
  margin: 5px;
  width: 85%;
  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 10px;
  color: white;
  /* font-size: 16px; */
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
  color: black;
  /* border-radius: 16px 0px 16px 0px; */
}
.result {
  margin-left: 5px;
  font-size: 12px;
  border-radius: 5px;
}
.result:hover {
  background-color: rgba(255, 255, 255, 0.25);
}

.path-element,
.mapboxgl-popup {
  font-family: "Futura", cursive;
  font-size: 10px;
  padding: 0;
  margin: 0;
  color: #424242;
}
.path-element {
  width: 100%;
  font-size: 12px;
  color: #bababa;
}
.mapboxgl-popup-content {
  margin: 0;
}
.path-element,
.mapboxgl-popup-content p {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  margin: 0 0 5px;
}
.path-element {
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}
.path-element:last-child {
  border-bottom: none;
}
.mapboxgl-popup-content p:last-child {
  border-bottom: none;
  margin: 0 0 0 0;
}
.mapboxgl-popup-content p b {
  font-size: 12px;
  color: #212121;
}
.path-element td b {
  font-size: 14px;
  color: #dcdcdc;
}
.total {
  background-color: rgba(50, 50, 50, 0.75);
  border-radius: 10px;
}
.total td b {
  color: white;
}
.total td {
  font-size: 14px;
}
